<template>
  <div class="long-page">
    <div class="information-box">
      <div class="information-nav">
        <p
          @click="data.navType = '0'"
          :class="{ active: data.navType === '0' }"
        >
          文档目录
        </p>
        <p
          @click="data.navType = '1'"
          :class="{ active: data.navType === '1' }"
        >
          匹配定位
        </p>
      </div>
      <div class="information-list">
        <div class="list-box" v-show="data.navType === '0'">
          <p v-if="!data.longTextCatalog.length" class="list-tips">
            无目录信息
          </p>
          <catalog-nav :catalogArr="data.longTextCatalog"></catalog-nav>
        </div>
        <div class="list-box" v-show="data.navType === '1'">
          <ul class="text-list">
            <p v-if="!data.matching.length" class="list-tips">暂无匹配定位</p>
            <li
              class="text-list-item"
              v-for="(item, index) in data.matching"
              :key="index"
              v-html="item.content"
              @click="switchHighlight($event, index)"
            ></li>
          </ul>
        </div>
      </div>
    </div>
    <div class="long-page-right">
      <div class="long-page-right-box">
        <!-- 设置模块 -->
        <div class="set-up">
          <div class="upload-file">
            选择文件
            <input
              class="read-file"
              type="file"
              name="file"
              multiple="multiple"
              id="files"
              @change="choosefile"
            />
          </div>
          <!-- 搜索框 -->
          <div class="query-input-box">
            <input
              class="query-input"
              type="text"
              placeholder="请输入关键词在正文中搜索"
              v-model="data.queryKeyWord"
              @keydown.enter="startQuery"
            />
            <p class="query-btn iconfont icon-sousuo" @click="startQuery"></p>
          </div>
          <!-- 数量 -->
          <div class="query-type-num">
            <div class="key-word-position">
              <span
                class="iconfont icon-qiehuanqizuo"
                @click="switchHighlight('prev')"
              ></span>
              <span>{{ data.Knum }}</span> <span>/</span>
              <span>{{ data.Ktotal }}</span>
              <span
                class="iconfont icon-qiehuanqiyou"
                @click="switchHighlight('next')"
              ></span>
            </div>
          </div>
          <!-- 查询类型选择 -->
          <div class="query-type-box">
            <p
              @click="data.queryType = '0'"
              :class="{ active: data.queryType === '0' }"
            >
              同篇
            </p>
            <p
              @click="data.queryType = '1'"
              :class="{ active: data.queryType === '1' }"
            >
              同段
            </p>
            <p
              @click="data.queryType = '2'"
              :class="{ active: data.queryType === '2' }"
            >
              同句
            </p>
          </div>
          <!-- 配置 -->
          <div class="set-box">
            <p class="set-box-btn" @click="data.setShow = true">配置</p>
            <!-- v-if="data.setShow" -->
            <div class="set-item-box" v-if="data.setShow">
              <div class="set-item">
                <p class="set-text">目录层级：</p>
                <input
                  class="set-input"
                  type="number"
                  v-model="data.hierarchyNum"
                />
              </div>
              <div class="set-item">
                <p class="set-text">截取个数：</p>
                <input
                  class="set-input"
                  type="number"
                  v-model="data.interceptionNum"
                />
              </div>
              <p class="set-tips">修改层级需从新选择不同文件生效</p>
              <p class="set-tips">修改截取需从新检索新关键词生效</p>
              <div class="ok-btn" @click="data.setShow = false">确认</div>
            </div>
          </div>
        </div>

        <div class="long-box">
          <!-- 长文本模块 -->
          <longTextTpl
            :fileList="data.fileList"
            :longKeyWordObj="data.longKeyWordObj"
            :switchHighObj="data.switchHighObj"
            :hierarchyNum="data.hierarchyNum"
            @getKtotal="getKtotal"
            @getKnum="getKnum"
            @getLongTextCatalog="getLongTextCatalog"
            @getCatalogue="getCatalogue"
          ></longTextTpl>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, onMounted } from 'vue';
import longTextTpl from './longTextTpl/longTextTpl.vue';
import catalogNav from './longText/CatalogNav.vue';
export default {
  components: {
    longTextTpl,
    catalogNav
  },
  name: '',
  setup() {
    const data = reactive({
      hierarchyNum: 0, //目录层级
      interceptionNum: 20, //截取个数
      setShow: false, //配置显示
      fileList: null, //文件元素
      longKeyWordObj: null, //需要传递的查询词
      queryKeyWord: '', //输入框查询词
      queryType: '0', //查询类型
      Ktotal: 0,
      Knum: 0,
      switchHighObj: null, //上下切换对象
      switchChange: true, //上下切换改变状态
      navType: '0', //0 目录  1 定位
      longTextCatalog: [], //目录
      matching: [] //匹配定位目录
    });
    // onMounted(() => {
    //   startQuery();
    //   data.queryKeyWord = '';
    // });
    // 文件变化
    const choosefile = () => {
      const fileList = document.getElementById('files').files[0];
      data.fileList = fileList;
    };
    // 关键词变化
    const startQuery = () => {
      data.longKeyWordObj = {
        queryKeyWord: data.queryKeyWord, //关键词
        queryType: data.queryType, //类型
        interceptionNum: parseInt(data.interceptionNum)
      };
    };
    // 获取查询的总数
    const getKtotal = (num) => {
      data.Ktotal = num;
    };
    // 获取当前位置
    const getKnum = (num) => {
      data.Knum = num;
    };
    // 上下切换高亮
    const switchHighlight = (type, index) => {
      if (data.Ktotal < 2) return;
      data.switchHighObj = {
        type,
        index,
        change: !data.switchChange
      };
    };
    // 获取目录
    const getLongTextCatalog = (catalog) => {
      if (catalog.length) {
        data.navType = '0';
      }
      data.longTextCatalog = catalog;
    };
    // 获取匹配定位目录
    const getCatalogue = (catalogue) => {
      if (catalogue.length) {
        data.navType = '1';
      }
      data.matching = catalogue;
    };
    return {
      choosefile,
      data,
      startQuery,
      getKtotal,
      getKnum,
      switchHighlight,
      getLongTextCatalog,
      getCatalogue
    };
  }
};
</script>
